    computed: mapGetters([
        "detail","user"
    ]),
    methods: {
        gwc(){
            if(this.user.error==1){
               alert("清闲登录");
            }else{
                axios({
                    method: 'post',
                    url: '/api/buycar',
                    data: {
                        user_id: this.user.data[0]._id,
                        kuanshi:this.detail.kuanshi,
                        bianhao_id: this.detail.bianhao,
                        num:"1",
                        newprice:this.detail.newprice,
                        pinpai:this.detail.pinpai,
                        ico:this.detail.ico
                    }
                }).then(
                    res => alert(res.data.mess)
                );
            }
           
        }
    },
    mounted () {
        let id = this.$route.params.id
        this.$store.dispatch(types.UPDATE_DEATIL,id) 
    },
}
